@import url("../common/css/global.css");
.nav-bar {
    width: 100%;
    height: var(--nav-bar-height);
    position: fixed;
    bottom: 0;
    background: #fff;
    -webkit-box-shadow: 0px -1px 2px rgba(128, 128, 128, 0.1);
            box-shadow: 0px -1px 2px rgba(128, 128, 128, 0.1);
}

.nav-bar>ul {
    list-style: none;
    margin: 0;
    width: 100%;
    height: var(--nav-bar-height);
    padding: 0;
    display: -ms-flexbox;
    display: flex;
}

.nav-bar>ul>li {
    -ms-flex: 1 1;
        flex: 1 1;
    height: var(--nav-bar-height);
    background-image: url(../icons/loading.png);
}


